import React,{useState,useRef} from 'react'
import { InputText } from "primereact/inputtext";
import { Button } from 'primereact/button';
// import Cookies from 'js-cookie';
import MenuAdmin from '../menu/MenuAdmin';
import { Toast } from 'primereact/toast';
export default function AjoutPublication() {
    const [nom, setNom] = useState('');
    const [description, setDescription] = useState('');
    const [selectedFile, setSelectedFile] = useState(null);

    const handleFileInputChange = (event) => {
      setSelectedFile(event.target.files[0]);
    }
    // const id=Cookies.get('userId');
    const toast = useRef(null);
    const showError = () => {
        toast.current.show({severity:'error', summary: 'Error', detail:'Message Content', life: 3000});
    }
    const formData = new FormData();
    formData.append('nom', nom);
    formData.append('description', description);
    formData.append('file', selectedFile);
    function handleSubmit(event) {
        event.preventDefault(); // empche la soumission du formulaire
      
        fetch('http://ia-back-production.up.railway.app/ajoutPub', {
          method: 'POST',
        //   headers: { 'Content-Type': undefined },
          body: formData,
          credentials: 'include'
        })
        .then(response => {
          if (response.ok) {
            // si la connexion a russi, redirigez l'utilisateur vers une page de russite
            window.location.href = '/listPub';
          } else {
            // si la connexion a chou, affichez un message d'erreur
            showError()
          }
        })
        .catch(error => {
          console.error(error);
        });
    };

  return (
    <div>
        <MenuAdmin/>
        <h1>Ajouter publication</h1>
        <Toast ref={toast} />
        <form onSubmit={handleSubmit} encType="multipart/form-data">
        <div className="card flex flex-column align-items-center gap-3 ">
        <div className="card">
            <input type="file" name="file" accept="image/*" onChange={handleFileInputChange} />
            </div>
            <InputText value={nom} onChange={(e) => setNom(e.target.value)} type="text" className="p-inputtext-sm" placeholder="Nom IA" />
            <InputText value={description} onChange={(e) => setDescription(e.target.value)}type="text" className="p-inputtext-sm" placeholder="Description" />
            <Button label="Ajouter publication" /> 
        </div>
        </form>
    </div>
  )
}