import React, { useState, useEffect } from 'react';
import MenuAdmin from '../menu/MenuAdmin';
import { Button } from 'primereact/button';
import { InputText } from 'primereact/inputtext';
import { DataView } from 'primereact/dataview';
import { useNavigate } from 'react-router-dom';
        
        
function ListPublication() {
  const navigate = useNavigate();
  const [publications, setPublications] = useState([]);
  useEffect(() => {
    fetch('http://ia-back-production.up.railway.app/listPub')
      .then(response => response.json())
      .then(data => setPublications(data))
      .catch(error => console.error(error));
  }, []);
  function deleteProduct(id) {
    fetch(`http://ia-back-production.up.railway.app/deletePub`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json'
      },
      body: JSON.stringify({id})
    })
    .then(response => {
      if (response.ok) {
        // Si la suppression russit, mettez  jour la liste des produits
        navigate('/listPub');
      } else {
        // Si la suppression choue, affichez un message d'erreur
        console.log('Une erreur s\'est produite lors de la suppression du produit.');
      }
    })
    .catch(error => {
      console.log(`Une erreur s'est produite lors de la suppression du produit : ${error.message}`);
    });
  }
//list paozy
  
  const itemTemplate = (publication) => {
    return (
        <div className="col-12">
            <div className="flex flex-column xl:flex-row xl:align-items-start p-4 gap-4">
            <img className="w-9 sm:w-32rem xl:w-24rem shadow-2 block xl:block mx-auto border-round" src={`data:image/${publication.extimg};base64,${publication.imagePub}`} alt={publication.nom} />
                <div className="flex flex-column sm:flex-row justify-content-between align-items-center xl:align-items-start flex-1 gap-4">
                    <div className="flex flex-column align-items-center sm:align-items-start gap-3">
                        <div className="text-2xl font-bold text-900">{publication.nom}</div>
                        <span className="flex align-items-center gap-2">
                                    {publication.descript}
                        </span>
                    </div>
                {/* <div className="flex align-items-center gap-3">
                                <span className="flex align-items-center gap-2">
                                    {publication.descript}
                                </span>
                </div> */}
                    <div className="flex sm:flex-column align-items-center sm:align-items-end gap-3 sm:gap-2">
                        <Button icon="pi pi-times" style={{ color: 'red' }} className="p-button-rounded" onClick={() => deleteProduct(publication.idPublication)}></Button>
                    </div>
                </div>
            </div>
        </div>
    );
    //delete pub
    
    
  };
  return (
    <div>
      <MenuAdmin/>
      <h1>Liste des publications</h1>
      <span className="p-input-icon-left">
          <i className="pi pi-search" />
          <InputText placeholder="Search" />
      </span>
        <div className="card">
            <DataView value={publications} itemTemplate={itemTemplate} />
        </div>
    </div>
  );
}

export default ListPublication;
