import React ,{useState,useRef }from 'react';
import { InputText } from 'primereact/inputtext';
import { Password } from 'primereact/password'
import { Button } from 'primereact/button';
import { Toast } from 'primereact/toast';
import { useNavigate } from 'react-router-dom';

function LoginAdmin() {
  const navigate = useNavigate();
    const [identification, setIdentification] = useState('');
    const [mdp, setMdp] = useState('');
    const toast = useRef(null);
    const showError = () => {
      toast.current.show({severity:'error', summary: 'Error', detail:'Message Content', life: 3000});
    }

    function handleSubmit(event) {
        event.preventDefault(); // empche la soumission du formulaire
      
        fetch('http://ia-back-production.up.railway.app/loginAdmin', {
          method: 'POST',
          headers: { 'Content-Type': 'application/json' },
          body: JSON.stringify({ identification, mdp }),
          credentials: 'include'
        })
        .then(response => {
          if (response.ok) {
            // si la connexion a russi, redirigez l'utilisateur vers une page de russite
            navigate('/listPub');
          } else {
            // si la connexion a chou, affichez un message d'erreur
            showError()
          }
        })
        .catch(error => {
          console.error(error);
        });
    };
    
    return (
        <div>
            <h1>Login admin</h1>
            
        <Toast ref={toast} />
            <form onSubmit={handleSubmit}>
                <div className="card flex justify-content-center">
            
                    <p>Identification</p>
                    <InputText value={identification} onChange={(e) => setIdentification(e.target.value)} />
                    <p>Password</p>
                    <Password value={mdp} onChange={(e) => setMdp(e.target.value)} feedback={false} />
                    <Button label="Submit" />
                </div>
            </form>
            
        </div>
    );
}

export default LoginAdmin;
